body .containers .card .box .content h2, .contetnt2 {
  position: absolute;
  top: 50px;
  right: 30px;
  font-size: 8rem;
  color: rgba(255, 210, 48, 0.2);
}

body .containers .card .box .content h3 {
  font-family: 'Lato', sans-serif;
  font-size: 2rem;
  color: #fff;
  z-index: 1;
  transition: 0.5s;
  margin-bottom: 0px;
}

